.footer {
  text-align: center;
  max-width: 320px;
  @include mq(tablet) {
    max-width: 768px;
  }
  @include mq(desktop) {
    max-width: 1280px;
  }
  padding: 30px;
  font-size: 16px;
  line-height: 1.19;
  background-color: var(--just-white);
  color: var(--dark-body-theme);
}
.footer__icon_logo {
  animation: heartbeat 1250ms cubic-bezier(.25, .1, .25, 1) infinite;
}
.footer a {
  text-decoration: underline;
  color: var(--secondary-text-color);
}
